.header {
    background-color: #FF714345;
}

.header_content {
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
    max-height: 145px;
    max-width: 1440px;
    padding: 38px 147px 49px 191px;
    align-items: center;
}

.header_items {
    & li {
        display: inline-block;
        list-style-type: none;
        padding: 0 49px 0;
    }
}

.photo {
    max-width: 200px;
    max-height: 39px;
}

.header_link {
    &:hover {
        color: #19191B;
        transition: 300ms;
    }
    text-decoration: none;
    font-weight: bold;
    font-size: 15px;
    line-height: 20px;
    letter-spacing: -0.468947px;
    color: #696871;
}

.btn {
    border: 0;
    width: 212px;
    height: 60px;
    border-radius: 10px;
    font-size: 17px;
    line-height: 22px;
    letter-spacing: -0.531474px;
    text-align: center;
    &:hover{
        cursor: pointer;
    }
}
.btn_orange {
    color: white;
    background-color: #FF7143;
}