.footer {
    background-color: #FF714345;
}

.footer_content {
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
    height: 145px;
    max-width: 1440px;
    align-items: center;
}

.footer_li {
    display: inline-block;
    list-style-type: none;
    padding: 0 49px 0;
}
.footer_link{
    &:hover {
        color: #19191B;
        transition: 300ms;
    }
    text-decoration: none;
    font-weight: bold;
    font-size: 15px;
    line-height: 20px;
    letter-spacing: -0.468947px;
    color: #696871;
}
.footer_img{
    max-width: 201px;
}